<template>
  <b-form class="mb-3">
    <b-img class="mb-4" :src="require('../assets/images/madcrew-logo.png')" width="72" height="57" />
<!--   <b-card-text class="h3 mb-3 fw-normal">Please sign in</b-card-text>-->
    <b-card header="MADCREW">
      <b-container class="mt-4">
        <b-row>
          <h2 class="pb-2 border-bottom">Kakao User</h2>
          <b-col>
            <svg v-show="!isAuthCode || !isGetUser" class="bd-placeholder-img rounded-circle" width="140" height="140" xmlns="http://www.w3.org/2000/svg" role="img" aria-label="Placeholder: 140x140" preserveAspectRatio="xMidYMid slice" focusable="false"><title>Placeholder</title><rect width="100%" height="100%" fill="#777"/><text x="50%" y="50%" fill="#777" dy=".3em">140x140</text></svg>
<!--            <b-img v-show="authorizationCode" :src="require(user.thumbnailImage)" width="140" height="140" />-->
            <img v-show="isAuthCode && isGetUser" style="border-radius: 50%" :src="user.thumbnailImage" width="110" height="110" />
            <h2 class="fw-normal mt-2">{{ user.name ? user.name : 'User Name' }}</h2>
            <p>{{ user.email ? user.email : 'username@email.com' }}</p>
          </b-col>
        </b-row>
      </b-container>

      <!--
      <b-form-group label="Email Address" label-for="loginEmail" class="inline-mb-0" floating>
        <b-form-input type="email" class="form-control" id="loginEmail" placeholder="name@example.com" trim/>
      </b-form-group>
      <b-form-group label="Password" label-for="loginPassword" floating>
        <b-form-input type="password" class="form-control" id="loginPassword" placeholder="Password" trim />
      </b-form-group>
      <label>
        <b-form-checkbox id="rememberLoginEmail" value="Y" unchecked-value="N">Remeber Me</b-form-checkbox>
      </label>
      -->
    </b-card>

    <b-button v-show="!isAuthCode && !isGetUser" class="w-100 btn btn-lg btn-primary mt-3" type="button" @click="kakaoLogin">Kakao Login</b-button>
    <b-button v-show="isAuthCode && !isGetUser" class="w-100 btn btn-lg btn-primary mt-3" type="button" @click="getKakaoUser">User</b-button>
    <b-card-text class="mt-5 mb-3 text-muted">&copy; MADCREW 1998-2023</b-card-text>
  </b-form>
</template>

<script>
import axios from 'axios'

export default {
  name: 'LoginForm',
  data () {
    return {
      authorizationCode: this.$route.query.code ? this.$route.query.code : null,
      accessToken: null,
      isAuthCode: this.$route.query.code,
      isGetUser: false,
      user: {
        name: '',
        thumbnailImage: '',
        email: ''
      }
    }
  },
  created () {
    console.log(this.authorizationCode)

    // Remove URL Parameter
    history.replaceState({}, null, location.pathname)

    if (this.authorizationCode) {
      this.getKakaoAccessToken()
    }
  },
  mounted () {
    this.initAppClass()
  },
  methods: {
    initAppClass () {
      document.querySelector('#app').classList.add('form-signin', 'w-100', 'm-auto')
    },
    kakaoLogin () {
      if (!this.authorizationCode) {
        // Get Kakao Auth code
        window.Kakao.Auth.authorize({
          redirectUri: 'https://madcrew.github.io'
        })
      }
    },
    getKakaoAccessToken () {
      axios({
        method: 'post',
        url: 'https://kauth.kakao.com/oauth/token',
        headers: {
          'Content-type': 'application/x-www-form-urlencoded;charset=utf-8'
        },
        data: {
          grant_type: 'authorization_code',
          client_id: 'c3bd2a974a3303c9135c8a3797b81811',
          redirect_uri: 'https://madcrew.github.io',
          code: this.authorizationCode
        }
      }).then((response) => {
        console.log('[getKakaoAccessToken]' + response)
        console.log('[[getKakaoAccessToken - access_token] ' + response.data.access_token)

        this.accessToken = response.data.access_token
      }).catch((error) => {
        console.log(error)
      })
    },
    checkValidKakaoAuthToken () {
      axios({
        method: 'get',
        url: 'https://kapi.kakao.com/v1/user/access_token_info',
        headers: {
          Authorization: 'Bearer ' + this.accessToken
        }
      }).then((response) => {
        console.log('[checkValidKakaoAuthToken] ' + response)
      }).catch((error) => {
        console.log(error)
      })
    },
    getKakaoUser () {
      console.log('START2')

      axios({
        method: 'post',
        url: 'https://kapi.kakao.com/v2/user/me',
        headers: {
          'Content-type': 'application/x-www-form-urlencoded;charset=utf-8',
          Authorization: 'Bearer ' + this.accessToken
        }
      }).then((response) => {
        console.log('START')
        console.log('[getKakaoUser] ' + JSON.stringify(response))
        console.log('[getKakaoUser - id] ' + response.data.id)
        console.log('[getKakaoUser - properties] ' + JSON.stringify(response.data.properties))
        console.log('[getKakaoUser - kakao_account] ' + JSON.stringify(response.data.kakao_account))

        this.user.name = response.data.kakao_account.profile.nickname
        this.user.thumbnailImage = response.data.kakao_account.profile.thumbnail_image_url
        this.user.email = response.data.kakao_account.email

        console.log(this.user)

        this.isGetUser = true
      }).catch((error) => {
        console.log('START3')

        console.log(error)
      })

      console.log('START4')
    }
  }
}
</script>

